.App {
  margin: 2% 0;
}

.navbar-brand {
  text-align:  center;
}

.title-row h2, .title-row h3 {
  text-align: center;
}

.title-row {
  margin-bottom: 2%;
}

.wordcloud-row {
  height: 150px;
}

.carousel-row img {
  width:  100%;
}

#portfolioScroll img {
  max-width: 750px;
  width:  100%;
  margin:  30px 0;
}

.story-section {
  margin: 20px 0;
}

.story-section p, .story-section a {
  margin: 0 auto;
}

.comment-form {
  margin:  20px 0;
}

form div {
  margin:  10px 0;
}

.container {
  margin:  25px 0;
}

.writing-page {
  max-width:  750px;
  width:  80%;
  margin: 0 10%;
}

.ghg-page {
  margin:  0 5%;
}

.ghg-page h1 {
  margin: 0 0 15px 0;
}

.ghg-page h2 {
  margin: 0 0 5px 0;
}

.ghg-page form {
  margin:  0;
}

.ghg-page p {
  margin-bottom:  5px;
}

.ghg-page .chart-title {
  font-weight: bold;
  margin: 10px 0 5px 30px;
}

.ghg-page form label{
  margin: 0 5px 5px 0;
}

.ghg-page form select {
  width: 300px;
  margin: 0 0 0 5px;
}

.ghg-page form input {
  margin: 0 0 0 5px;
}
